import { Injectable } from '@angular/core';
import { MatTableDataSource } from '@angular/material';
import { Pigscale } from '../models/pigscale.model';
import { FormBuilder } from '@angular/forms';
import { environment } from 'src/environments/environment';
import { HttpClient } from '@angular/common/http';
import { Broilerscale } from '../models/broilerscale.model';
import { Truckscale } from '../models/truckscale.model';

@Injectable({
  providedIn: 'root'
})
export class ScaleService {

  pigList:MatTableDataSource<Pigscale>;
  broilerList:MatTableDataSource<Broilerscale>;
  truckList:MatTableDataSource<Truckscale>;
  constructor(private fb:FormBuilder,private http:HttpClient) { 

  }

  getPigList() {
    return this.http.get(environment.apiURL + '/PigScales').toPromise();
  }

  getBroilerList(){
    return this.http.get(environment.apiURL + '/BroilerScales').toPromise();
  }

  getTruckList(){
    return this.http.get(environment.apiURL + '/TruckScales').toPromise();
  }
}
