import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator, MatSort, MatTableDataSource } from '@angular/material';
import { RoleService } from 'src/app/shared/role.service';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Role } from 'src/app/shared/role.model';
import { RoleCtlService } from 'src/app/shared/role-ctl.service';
import { Controllerinfo } from 'src/app/models/controllerinfo.model';
import { Actioninfo } from 'src/app/models/actioninfo.model';

@Component({
  selector: 'app-rolepermit',
  templateUrl: './rolepermit.component.html',
  styleUrls: ['./rolepermit.component.css']
})
export class RolepermitComponent implements OnInit {


  displayedColumns: string[] = ['Name','Permit'];
  controllerInfo:Controllerinfo[];
  actionInfo:Actioninfo[];
  role:Role;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  constructor(public service: RoleService,public serviceCtl:RoleCtlService,
    private router: Router,
    private toastr: ToastrService) { 
      this.controllerInfo = [];
      this.actionInfo = [];
    }

  ngOnInit() {
    this.service.getRolesList().then(res => {
      this.service.roleList = new MatTableDataSource(res as Array<Role>);
      this.service.roleList.paginator = this.paginator;
      this.service.roleList.sort = this.sort;
    })
  }

  onChangePermit(event,element){
    this.service.getRoleByID(element.Id).then(res => {
        this.role = res as Role;
        let jsonconvert = JSON.parse(this.role.Access);
        this.controllerInfo = jsonconvert;
        if(this.controllerInfo == null){
          this.controllerInfo = [];
        }
        if(event.value != ''){
          if(event.value == 'A'){
            this.onAllow();
          }
          else if(event.value == 'D'){
            this.onDisallow();
          }
          this.service.formModel.value.Id = element.Id;
          this.service.formModel.value.RoleName = element.Name;
          this.service.onPutRoles(this.controllerInfo).then(res => {
            let index = this.service.roleList.data.findIndex(x=>x.Id == element.Id);
            if(index >= 0){
              this.service.roleList.data[index].Access = JSON.stringify(this.controllerInfo);
              this.service.roleList._updateChangeSubscription();
            }
            this.toastr.success("Role updated","Role Access");
          })
        }      
    });

  }

  onAllow(){
      let index = this.controllerInfo.findIndex(x=>x.name == this.serviceCtl._parentnode);
      if(index <= -1){
        this.actionInfo.push({
          name:this.serviceCtl._childnode
        });
        this.controllerInfo.push({
          name:this.serviceCtl._parentnode,
          children:this.actionInfo
        });
        console.log(this.controllerInfo);
      }
      else{
        let indexact = this.controllerInfo[index].children.findIndex(x=>x.name == this.serviceCtl._childnode);
        if(indexact <= -1){
          this.controllerInfo[index].children.push({
            name:this.serviceCtl._childnode
          });
          console.log(this.controllerInfo);
        }
      }
  }

  onDisallow(){
    let index = this.controllerInfo.findIndex(x=>x.name == this.serviceCtl._parentnode);
    if(index >= 0){
      let indexact = this.controllerInfo[index].children.findIndex(x=>x.name == this.serviceCtl._childnode);
      if(indexact >= 0){
        this.controllerInfo[index].children.splice(indexact,1);
        console.log(this.controllerInfo);
      }
    }
  }

}
