import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule,ReactiveFormsModule } from "@angular/forms";
import { HttpClientModule, HTTP_INTERCEPTORS } from "@angular/common/http";
import { ToastrModule } from 'ngx-toastr';

import { AppRoutingModule } from './app-routing.module';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { AppComponent } from './app.component';
import { MenusComponent } from './menus/menus.component';
import { MaterialModule } from './material.module';
import { UserComponent } from './user/user.component';
import { RegistrationComponent } from './user/registration/registration.component';
import { UserService } from './shared/user.service';
import { LoginComponent } from './user/login/login.component';
import { HomeComponent } from './home/home.component';
import { AuthInterceptor } from './auth/auth.interceptor';
import { RolesComponent } from './roles/roles.component';
import { RoleComponent } from './roles/role/role.component';
import { RoleCtlComponent } from './roles/role-ctl/role-ctl.component';
import { RoleService } from './shared/role.service';
import { RoleCtlService } from './shared/role-ctl.service';
import { UserroleComponent } from './userroles/userrole/userrole.component';
import { UserrolelistComponent } from './userroles/userrolelist/userrolelist.component';
import { UserrolesComponent } from './userroles/userroles.component';
import { ForbiddenComponent } from './error/forbidden/forbidden.component';
import { UsereditComponent } from './user/useredit/useredit.component';
import { MAT_MOMENT_DATE_ADAPTER_OPTIONS } from '@angular/material-moment-adapter';
import { RoleAppComponent } from './role-app/role-app.component';
import { RoletreeComponent } from './role-app/roletree/roletree.component';
import { RolepermitComponent } from './role-app/rolepermit/rolepermit.component';
import { MenuAppComponent } from './menu-app/menu-app.component';
import { MenuappDetailComponent } from './menu-app/menuapp-detail/menuapp-detail.component';
import { MenuappListComponent } from './menu-app/menuapp-list/menuapp-list.component';
import { MenuappService } from './shared/menuapp.service';
import { MenuRootComponent } from './menu-root/menu-root.component';
import { MenurootDetailComponent } from './menu-root/menuroot-detail/menuroot-detail.component';
import { MenurootListComponent } from './menu-root/menuroot-list/menuroot-list.component';
import { UsermenusComponent } from './usermenus/usermenus.component';
import { ScaleService } from './shared/scale.service';
import { PigscaleComponent } from './pigscale/pigscale.component';
import { BroilerscaleComponent } from './broilerscale/broilerscale.component';
import { TruckfrmscaleComponent } from './truckfrmscale/truckfrmscale.component';

@NgModule({
  declarations: [
    AppComponent,
    MenusComponent,
    UserComponent,
    UsereditComponent,
    RegistrationComponent,
    LoginComponent,
    HomeComponent,
    RolesComponent,
    RoleComponent,
    RoleCtlComponent,
    UserroleComponent,
    UserrolelistComponent,
    UserrolesComponent,
    RoleAppComponent,
    RoletreeComponent,
    RolepermitComponent,
    MenuAppComponent,
    MenuappDetailComponent,
    MenuappListComponent,
    MenuRootComponent,
    MenurootDetailComponent,
    MenurootListComponent,
    UsermenusComponent,
    ForbiddenComponent,
    PigscaleComponent,
    BroilerscaleComponent,
    TruckfrmscaleComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    BrowserAnimationsModule,
    MaterialModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    BrowserAnimationsModule,
    ToastrModule.forRoot(),
  ],
  entryComponents:[UsereditComponent],
  providers: [UserService,RoleService,RoleCtlService,MenuappService,ScaleService,{
    provide: HTTP_INTERCEPTORS,
    useClass: AuthInterceptor,
    multi: true
  },
  { provide: MAT_MOMENT_DATE_ADAPTER_OPTIONS, useValue: { useUtc: true } }],
  bootstrap: [AppComponent]
})
export class AppModule { }
