import { Injectable } from '@angular/core';
import { Menuapp } from '../models/menuapp.model';
import { MatTableDataSource } from '@angular/material';
import { FormBuilder, Validators } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { Menuroot } from '../models/menuroot.model';

@Injectable({
  providedIn: 'root'
})
export class MenuappService {

  menuList:MatTableDataSource<Menuapp>;
  menurootList:MatTableDataSource<Menuroot>;
  constructor(private fb:FormBuilder,private http:HttpClient) { 

  }

  formModel = this.fb.group({
    MenuId:[''],
    RootId:['',Validators.required],
    Name:['',Validators.required],
    Icon:['',Validators.required],
    RouterLink:['',Validators.required]
  });

  formRoot = this.fb.group({
    MenuId:[''],
    RootMenuId:['',Validators.required],
    Icon:['',Validators.required],
    Label:['']
  });

  //=====================Menuapp

  postMenuapp(){
    var body = {
      RootId: this.formModel.value.RootId,
      Name: this.formModel.value.Name,
      Icon:this.formModel.value.Icon,
      RouterLink:this.formModel.value.RouterLink
    }
    return this.http.post(environment.apiURL + '/ApplicationMenu', body);
  }

  putMenuapp(){
    var body = {
      MenuId:this.formModel.value.MenuId,
      RootId: this.formModel.value.RootId,
      Name: this.formModel.value.Name,
      Icon:this.formModel.value.Icon,
      RouterLink:this.formModel.value.RouterLink
    }
    return this.http.put(environment.apiURL + '/ApplicationMenu/' + this.formModel.value.MenuId ,body);
  }

  getMenuappList() {
    return this.http.get(environment.apiURL + '/ApplicationMenu').toPromise();
  }

  getMenuappByID(id:string):any{
    return this.http.get(environment.apiURL + '/ApplicationMenu/'+id).toPromise();
  }

  deleteMenuapp(id:number) {
    return this.http.delete(environment.apiURL + '/ApplicationMenu/'+id).toPromise();
  }

//========================Menu Root

  postMenuroot(){
    var body = {
      RootMenuId: this.formRoot.value.RootMenuId,
      Icon: this.formRoot.value.Icon,
      Label:this.formRoot.value.Label
    }
    return this.http.post(environment.apiURL + '/ApplicationRootMenus', body);
  }

  putMenuroot(){
    var body = {
      MenuId:this.formRoot.value.MenuId,
      RootMenuId: this.formRoot.value.RootMenuId,
      Icon: this.formRoot.value.Icon,
      Label:this.formRoot.value.Label
    }
    return this.http.put(environment.apiURL + '/ApplicationRootMenus/' + this.formRoot.value.MenuId ,body);
  }

  getMenurootList() {
    return this.http.get(environment.apiURL + '/ApplicationRootMenus').toPromise();
  }

  getMenurootByID(id:number):any{
    return this.http.get(environment.apiURL + '/ApplicationRootMenus/'+id).toPromise();
  }

  deleteMenuroot(id:number) {
    return this.http.delete(environment.apiURL + '/ApplicationRootMenus/'+id).toPromise();
  }

  //===============Usermenu
  getUserMenurootList() {
    return this.http.get(environment.apiURL + '/ApplicationUserMenus').toPromise();
  }

  
  postUserMenuValidate(url:string){
    var body= {
      MenuId: 0,
      RootId: 0,
      Name:'',
      Icon:'',
      RouterLink:url
    }
    return this.http.post(environment.apiURL + '/ApplicationUserMenus/Validate', body).toPromise();
  }

}
