import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { UserComponent } from './user/user.component';
import { RegistrationComponent } from './user/registration/registration.component';
import { LoginComponent } from './user/login/login.component';
import { HomeComponent } from './home/home.component';
import { AuthGuard } from './auth/auth.guard';
import { RolesComponent } from './roles/roles.component';
import { RoleComponent } from './roles/role/role.component';
import { UserrolesComponent } from './userroles/userroles.component';
import { ForbiddenComponent } from './error/forbidden/forbidden.component';
import { RoleAppComponent } from './role-app/role-app.component';
import { MenuAppComponent } from './menu-app/menu-app.component';
import { MenuRootComponent } from './menu-root/menu-root.component';
import { UsermenusComponent } from './usermenus/usermenus.component';
import { PigscaleComponent } from './pigscale/pigscale.component';
import { BroilerscaleComponent } from './broilerscale/broilerscale.component';
import { TruckfrmscaleComponent } from './truckfrmscale/truckfrmscale.component';

const routes: Routes = [
  {path:'user',component:UserComponent,children:[
      {path:'registration',component:RegistrationComponent,canActivate:[AuthGuard]},
      {path:'login',component:LoginComponent}
  ]},
  {path:'error/403',component:ForbiddenComponent},
  {path:'home',component:HomeComponent,canActivate:[AuthGuard]},
  {path:'roleapp',component:RoleAppComponent,canActivate:[AuthGuard]},
  {path:'menuapp',component:MenuAppComponent,canActivate:[AuthGuard]},
  {path:'menuroot',component:MenuRootComponent,canActivate:[AuthGuard]},
  {path:'usermenu',component:UsermenusComponent,canActivate:[AuthGuard]},
  {path:'roles',component:RolesComponent},
  {
    path:'userroles',component:UserrolesComponent,canActivate:[AuthGuard]
  },
  {path:'pigscale',component:PigscaleComponent,canActivate:[AuthGuard]},
  {path:'broilerscale',component:BroilerscaleComponent,canActivate:[AuthGuard]},
  {path:'truckscale',component:TruckfrmscaleComponent,canActivate:[AuthGuard]},
  { path : '', redirectTo:'/user/login', pathMatch : 'full'}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
