import { Component, OnInit, ViewChild } from '@angular/core';
import { ScaleService } from '../shared/scale.service';
import { ToastrService } from 'ngx-toastr';
import { Router } from '@angular/router';
import { MatPaginator, MatSort, MatTableDataSource } from '@angular/material';
import { Pigscale } from '../models/pigscale.model';

@Component({
  selector: 'app-pigscale',
  templateUrl: './pigscale.component.html',
  styleUrls: ['./pigscale.component.css']
})
export class PigscaleComponent implements OnInit {

  displayedColumns: string[] = ['CustomerID','CustomerName','PigCode','Weight','DateOfScale','ProjectID','Type','UnitPrice','Delete'];
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  constructor(public service:ScaleService,private router: Router,
    private toastr:ToastrService) { }

  ngOnInit() {
    this.service.getPigList().then(res => {
      this.service.pigList = new MatTableDataSource(res as Array<Pigscale>);
      this.service.pigList.paginator = this.paginator;
      this.service.pigList.sort = this.sort;
    })
  }

}
