import { Component, OnInit } from '@angular/core';
import { Controllerinfo } from 'src/app/models/controllerinfo.model';
import { FlatTreeControl } from '@angular/cdk/tree';
import { MatTreeFlattener, MatTreeFlatDataSource } from '@angular/material';
import { RoleCtlService } from 'src/app/shared/role-ctl.service';
import { RoleService } from 'src/app/shared/role.service';

interface AppNode {
  name: string;
  children?: AppNode[];
}

interface ExampleFlatNode {
  expandable: boolean;
  name: string;
  level: number;
}

@Component({
  selector: 'app-roletree',
  templateUrl: './roletree.component.html',
  styleUrls: ['./roletree.component.css']
})
export class RoletreeComponent implements OnInit {

  public controllerInfo:Controllerinfo[];
  private _transformer = (node: AppNode, level: number) => {
    return {
      expandable: !!node.children && node.children.length > 0,
      name: node.name,
      level: level,
    };
  }

  treeControl = new FlatTreeControl<ExampleFlatNode>(node => node.level, node => node.expandable);
  treeFlattener = new MatTreeFlattener(this._transformer, node => node.level, node => node.expandable, node => node.children);
  dataSource = new MatTreeFlatDataSource(this.treeControl, this.treeFlattener);
  hasChild = (_: number, node: ExampleFlatNode) => node.expandable;
  getLevel = (node: ExampleFlatNode) => node.level;

  constructor(private serviceCtl:RoleCtlService,private service:RoleService) { 
    this.controllerInfo = [];
    this.getCtl();
  }

  ngOnInit() {
  }

  getCtl(){
    this.serviceCtl.getTreeList().then((res:any) => {
      this.controllerInfo = res as Controllerinfo[];
      const TREE_DATA:AppNode[] = this.controllerInfo;
      this.dataSource.data = TREE_DATA;
    });
  }
  
  /* Get the parent node of a node */
  getParentNode(node: ExampleFlatNode): ExampleFlatNode | null {
    const currentLevel = this.getLevel(node);
    if (currentLevel < 1) {
      return null;
    }
    const startIndex = this.treeControl.dataNodes.indexOf(node) - 1;
    for (let i = startIndex; i >= 0; i--) {
      const currentNode = this.treeControl.dataNodes[i];
      if (this.getLevel(currentNode) < currentLevel) {
        return currentNode;
      }
    }
    return null;
  }
  
  onClickNode(node: ExampleFlatNode):void {
    let parent: ExampleFlatNode | null = this.getParentNode(node);
    this.serviceCtl._parentnode = parent.name;
    this.serviceCtl._childnode = node.name;
    for (var i = 0; i < this.service.roleList.data.length; i++) {
      let jsonconv = JSON.parse(this.service.roleList.data[i].Access);
      this.controllerInfo = jsonconv;
      if(this.controllerInfo != null){
        let index = this.controllerInfo.findIndex(x=>x.name == this.serviceCtl._parentnode);
        if(index >= 0){
          let indexact = this.controllerInfo[index].children.findIndex(x=>x.name == this.serviceCtl._childnode);
          if(indexact >= 0){
            this.service.roleList.data[i].Selected = 'A';
          }
          else{
            this.service.roleList.data[i].Selected = 'D';
          }
        }
        else{
          this.service.roleList.data[i].Selected = 'N';
        }
      }
      else{
        this.service.roleList.data[i].Selected = 'N';
      }
    }
    this.service.roleList._updateChangeSubscription();
  }
}
