import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MenuappService } from '../shared/menuapp.service';
import { Menuapp } from '../models/menuapp.model';

@Component({
  selector: 'app-menus',
  templateUrl: './menus.component.html',
  styleUrls: ['./menus.component.css']
})
export class MenusComponent implements OnInit {

  menuList:Menuapp[];
  rootsetting:Menuapp[];
  rootscale:Menuapp[];
  constructor(private router: Router,private service:MenuappService) { }

  ngOnInit() {
    this.menuList = [];
    this.rootsetting = [];
    this.rootscale = [];
    this.service.getUserMenurootList().then(res => {
      this.menuList = res as Array<Menuapp>;
      if(this.menuList.length > 0){
        this.rootsetting = this.menuList.filter(x => x.Rootname == 'setting');
        this.rootscale = this.menuList.filter(x=>x.Rootname == 'scale');
        localStorage.setItem('menus',JSON.stringify(this.menuList));
      }
    });
  }

  
  onLogout() {
    localStorage.removeItem('token');
    localStorage.removeItem('menus');
    this.router.navigate(['/user/login']);
  }

}
