import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator, MatSort, MatTableDataSource } from '@angular/material';
import { ScaleService } from '../shared/scale.service';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Broilerscale } from '../models/broilerscale.model';

@Component({
  selector: 'app-broilerscale',
  templateUrl: './broilerscale.component.html',
  styleUrls: ['./broilerscale.component.css']
})
export class BroilerscaleComponent implements OnInit {

  displayedColumns: string[] = ['CustomerID','CustomerName','TypeOfScale','Weight','DateOfScale','ProjectID','TypeOfItem','NumOfTrap','NumOfChicken','UnitPrice','LocationID','Delete'];
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  constructor(public service:ScaleService,private router: Router,
    private toastr:ToastrService) { }

  ngOnInit() {
    this.service.getPigList().then(res => {
      this.service.broilerList = new MatTableDataSource(res as Array<Broilerscale>);
      this.service.broilerList.paginator = this.paginator;
      this.service.broilerList.sort = this.sort;
    })
  }

}
