import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot,Router } from '@angular/router';
import { Observable, Subject, BehaviorSubject } from 'rxjs';
import { MenuappService } from '../shared/menuapp.service';
import { Menuapp } from '../models/menuapp.model';

@Injectable({
  providedIn: 'root'
})
export class AuthGuard implements CanActivate {
  private menuApplist:Menuapp[];
  constructor(private router: Router,private service:MenuappService){
    this.menuApplist = [];

  }
  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): boolean {
    if (localStorage.getItem('token') != null){
      let isexist = this.validateMenuExisting(state.url);
      if(isexist){
        return true;
      }
      else{
        this.router.navigate(['/home']);
        return false;
      }
    }
    else {
      this.router.navigate(['/user/login']);
      return false;
  }
  //return true;
}

validateMenuExisting(url:string):boolean{
  let strmenu = localStorage.getItem('menus');
  if(strmenu == null){
    return true
  }
  else{
    this.menuApplist = JSON.parse(strmenu);
    let index = this.menuApplist.findIndex(x=>x.RouterLink == url);
    if(index >= 0){
      return true;
    }
    else{
      return false;
    }
  }
}

}