import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator, MatSort, MatTableDataSource } from '@angular/material';
import { ScaleService } from '../shared/scale.service';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Truckscale } from '../models/truckscale.model';

@Component({
  selector: 'app-truckfrmscale',
  templateUrl: './truckfrmscale.component.html',
  styleUrls: ['./truckfrmscale.component.css']
})
export class TruckfrmscaleComponent implements OnInit {

  displayedColumns: string[] = ['RefNbr','TruckID','CustomerID','CustomerName','TruckWeight','TotalWeight','ItemWeight','Description','DateOfScale','SecondDateOfScale','Location','Delete'];
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  constructor(public service:ScaleService,private router: Router,
    private toastr:ToastrService) { }

  ngOnInit() {
    this.service.getPigList().then(res => {
      this.service.truckList = new MatTableDataSource(res as Array<Truckscale>);
      this.service.truckList.paginator = this.paginator;
      this.service.truckList.sort = this.sort;
    })
  }

}
